.ckeditorContent p, .cke_editable p, .ckeditorContent ul, .cke_editable ul, .ckeditorContent li, .cke_editable li, .ckeditorContent a, .cke_editable a {
  font-size: 1.6rem; }

.ckeditorContent h2, .cke_editable h2 {
  font-size: 1.8rem;
  font-weight: 500; }

.ckeditorContent h3, .cke_editable h3 {
  font-size: 1.6rem;
  font-weight: 500; }

.ckeditorContent h2, .cke_editable h2, .ckeditorContent h3, .cke_editable h3, .ckeditorContent h4, .cke_editable h4, .ckeditorContent h5, .cke_editable h5, .ckeditorContent h6, .cke_editable h6 {
  color: var(--color-blue-1); }

.ckeditorContent h2.wysiwyg-title, .cke_editable h2.wysiwyg-title {
  font-weight: 700;
  color: var(--theme-color-1); }

.ckeditorContent h3.wysiwyg-title, .cke_editable h3.wysiwyg-title {
  font-weight: 500;
  color: var(--color-blue-1); }

.ckeditorContent ul, .cke_editable ul {
  margin: 0.5rem 0;
  padding-left: 2rem; }
  .ckeditorContent ul li, .cke_editable ul li {
    list-style-type: disc; }

.ckeditorContent ol, .cke_editable ol {
  margin: 0.5rem 0;
  padding-left: 2rem; }
  .ckeditorContent ol li, .cke_editable ol li {
    list-style-type: decimal; }

.ckeditorContent .text-align-center, .cke_editable .text-align-center {
  text-align: center; }

.ckeditorContent .text-align-left, .cke_editable .text-align-left {
  text-align: left; }

.ckeditorContent .text-align-right, .cke_editable .text-align-right {
  text-align: right; }

.ckeditorContent strong, .cke_editable strong {
  font-weight: 500; }

.ckeditorContent em, .cke_editable em {
  font-style: italic; }

.ckeditorContent a, .cke_editable a {
  text-decoration: underline;
  font-weight: 500; }
  .ckeditorContent a[rel="noopener"], .cke_editable a[rel="noopener"], .ckeditorContent a[rel="noreferrer"], .cke_editable a[rel="noreferrer"] {
    color: var(--link-color); }
  .ckeditorContent a:visited, .cke_editable a:visited {
    color: var(--link-visited-color); }
  .ckeditorContent a:hover, .cke_editable a:hover, .ckeditorContent a:focus, .cke_editable a:focus {
    text-decoration: none; }

html {
  --font-size: 62.5%;
  --font-main: 'Roboto', sans-serif;
  --font-title: 'Roboto', sans-serif;
  --font-cursive: 'mrs-eaves-xl-serif-narrow', 'Roboto', sans-serif;
  --theme-color-1: #17323F;
  --color-blue-1: #06475E;
  --text-color: #212529;
  --link-color: #006487;
  --link-visited-color: #660099;
  font-size: var(--font-size);
  font-family: var(--font-main); }
